<template>
    <CCard class="p-4">
        <CCardBody>
            <h4>Create User</h4>
            <hr />
            <form>
                <v-text-field
                    v-model="name"
                    label="Name"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-row>
                    <c-col md="6" sm="6">
                        <v-text-field
                            v-model="phone"
                            label="Phone"
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                    </c-col>
                    <c-col md="6" sm="6">
                        <v-text-field
                            v-model="email"
                            label="Email"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                    </c-col>
                </v-row>
                <v-text-field
                    v-model="password"
                    label="Password"
                    :append-icon="passwordVisible ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="passwordVisible ? 'text' : 'password'"
                    @click:append="passwordVisible = !passwordVisible"
                    required
                    outlined
                    dense
                    clearable
                ></v-text-field>
                <v-select
                    v-model="role"
                    :items="roles"
                    label="Role"
                    required
                    outlined
                    dense
                ></v-select>
                <CButton @click="submit" color="primary" class="px-4">
                    Submit
                    <v-progress-circular
                        v-if="loading"
                        indeterminate
                        color="primary"
                        :size="15"
                    ></v-progress-circular>
                </CButton>
            </form>
        </CCardBody>
    </CCard>
</template>

<script>
export default {
    name: "CreateUser",
    components: {},
    data() {
        return {
            name: "",
            phone: "",
            email: "",
            password: "",
            role: "",
            loading: false,
            passwordVisible: false,
            roles: ["admin", "employee"]
        };
    },
    methods: {
        submit() {
            let self = this;
            if (self.loading) {
                return;
            }
            self.loading = true;
            let data = {
                name: self.name,
                email: self.email,
                phone: self.phone,
                password: self.password,
                role: self.role
            };
            this.$store
                .dispatch("user/create", data)
                .then(response => {
                    self.loading = false;
                    self.$router.back();
                })
                .catch(error => {
                    self.loading = false;
                });
        }
    }
};
</script>

<style scoped>
.v-text-field >>> input {
    font-size: 0.8em;
    font-weight: 100;
}
.v-text-field >>> label {
    font-size: 0.8em;
}
.v-text-field >>> button {
    font-size: 0.8em;
}
</style>
